@import url(https://fonts.googleapis.com/css2?family=Amatic+SC&family=Montserrat:wght@400;500;600;700&display=swap);
/* # CSS */
:root {
  --fuenteOrquestarte:'Montserrat', sans-serif;
  --fuenteCitas: 'Amatic SC', cursive;
  --naranja: #F7882F;
  --gris: #6b7a8f;
  --amarillo: #f7c331;
  --cafe: #dcc7aa;
  --blanco:#ffffff;
}
html {
  box-sizing: border-box;
  font-size: 62.5%;
  padding: 0;
  margin: 0;
}
body {
  padding: 0;
  margin: 0;
}
*,*:before, *:after {
  box-sizing: inherit;
}
.contenedor {
  max-width: 90%;
  width: 90;
  margin: 0 auto;
}
a {
  text-decoration: none;
  font-family: 'Montserrat', sans-serif;
  font-family: var(--fuenteOrquestarte)
}
h1, h2, h3, h4, p {
  font-family: 'Montserrat', sans-serif;
  font-family: var(--fuenteOrquestarte);
}
h1 {
  font-size: 3.5rem;
  color: #F7882F;
  color: var(--naranja);
}
h3 {
  font-weight: 400;
  font-size: 2.3rem;
  color: #6b7a8f;
  color: var(--gris);
}

img {
  max-width: 100%;
}

.no-margin {
  margin: 0;
}
.no-padding {
  padding: 0;
}
.centrar-texto {
  text-align: center;
}
.negrita {
  font-weight: bold;
}

.barra {
  text-align: center;
}
.logo a {
  color: black;
}
.logo h2 {
  color: #F7882F;
  color: var(--naranja);
}

.logo__linea1 {
  margin-left: 1rem;
  margin-bottom: 0;
}
.logo__linea2 {
  margin-left: 1.25rem;
  margin-top: 0;
  margin-bottom: 3rem;
}

.navegacion__enlaces {
  color: #F7882F;
  color: var(--naranja);
  text-transform: uppercase;
  font-size: 2.3rem;
  font-weight: 500;
  display: block;
  margin-bottom: 2rem;    
}
@media (min-width: 1110px) {
 .barra {
     display: grid;
     grid-template-columns: 1fr 3fr;
 }
 .navegacion{
      display: flex;
      justify-content: space-around;
      align-items: center;
 }  
 .navegacion__enlaces {
     margin-top: 1.5rem;
     max-width: 250px;
 }
 .navegacion__enlaces__unalinea {
  margin-top: 2.5rem;
 }
}

.navegacion__enlaces:hover {
  color: #f7c331;
  color: var(--amarillo);
}
.titulo {
  text-align: center;
}
.footer {
  padding-top: 1rem;
  padding-bottom: 2rem;
  background-color: #6b7a8f;
  background-color: var(--gris);
}
.footer-presenciales {
  padding-top: 1rem;
  padding-bottom: 2rem;
  background-color: #6b7a8f;
  background-color: var(--gris);   
}
.mailto {
  color:#fff;
  display: block;
  margin-top: 1.5rem;
  font-size: 1.9rem;
}
blockquote {
  margin-top: 0;
  margin-bottom: 0;
  padding: 0;
}
.derechos {
  font-family: 'Montserrat', sans-serif;
  font-family: var(--fuenteOrquestarte);
  color: #ffffff;
  color: var(--blanco);
  font-size: 1.7rem;
  margin-bottom: 0;
  text-align: center;
}
.derechos p {
  font-size: 1.5rem;
}
.texto-derechos{
  margin-bottom: 0;
}

.contenedor-testimonial{
  width: 90%;
  max-width: 1100px;
  margin: auto;
}

.cuadro-testimonial {
  background-color: #f7c331;
  background-color: var(--amarillo);
  padding: 2rem;
  border-radius: 10px;
  margin-bottom: 5rem;
}

@media (min-width: 920px) {
   .cuadro-testimonial {
       display: grid;
       grid-template-columns: repeat(2, 1fr);
   }
}
@media (min-width: 1200px) {
    .cuadro-testimonial {
        padding-top:3rem;
        padding-bottom:3rem;
    }
}

@media (min-width: 1200px) {
   .imagen-testimonial {
       width: 90;
   }
}
.cita {
  display: flex;
  justify-items: center;
  align-items: center;
  text-align: center;
}
.texto-testimonial {
  color: #6b7a8f;
  color: var(--gris);
  font-family: 'Amatic SC', cursive;
  font-family: var(--fuenteCitas);
  font-size: 4rem;
  text-align: center;
  margin-top: 0;
  margin-bottom: 1.5rem;
}
.img-testimonial {
  display: flex;
  align-items: center;
  justify-content: center;
}
.img-testimonial img {
  border-radius: 10px;
  width: 100%;
  max-width: 500px;
  height: 400px;
}
@media (min-width: 1110px) {
   .texto-testimonial {
       font-size: 4.5rem;
   }
   
}
@media (min-width: 1200px) {
    .texto-testimonial {
        font-size: 6rem;
    }
}
.autor-testimonial {
  font-family: 'Montserrat', sans-serif;
  font-family: var(--fuenteOrquestarte);
  color: #6b7a8f;
  color: var(--gris);
  font-size: 3rem;
}
/* @media (min-width: 1110px) {
  .autor-testimonial {
      font-size: 3rem;
  }
} */


/* Recitales */

.contenedor-recitales {
  display: flex;
  flex-direction: column;
  align-items: center;
  grid-gap: 3rem;
  gap: 3rem;
}
.cuadro-recital{
  background-color: #f7c331;
  background-color: var(--amarillo);
  text-align: center;
  width: 30rem;
}
.cuadro-recital:last-child{
  margin-bottom: 3rem;
}

.cuadro-recital h1 {
  font-family: 'Amatic SC', cursive;
  font-family: var(--fuenteCitas);
  color: #6b7a8f;
  color: var(--gris);
  font-weight: 300;
}
.titulo-recital {
  font-family: 'Montserrat', sans-serif;
  font-family: var(--fuenteOrquestarte);
  text-transform: uppercase;
  color: #6b7a8f;
  color: var(--gris);
  font-weight: 300;
  font-size: 3rem;
  margin-bottom: 1rem;
}
.hora-recital {
  font-family: 'Montserrat', sans-serif;
  font-family: var(--fuenteOrquestarte);
  text-transform: uppercase;
  color: #F7882F;
  color: var(--naranja);
  font-weight: 300;
  font-size: 2.5rem;
  margin-top: 1rem;
}
.programa {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.pieza-flex {
  display: flex;
  justify-content: space-between;
  grid-gap: 2.5rem;
  gap: 2.5rem;
}
@media (max-width: 400px) {
  .repertorio {
      width: 35rem;
  }
}
.alumno, .pieza, .compositor, .maestro {
  font-family: 'Montserrat', sans-serif;
  font-family: var(--fuenteOrquestarte);
}
.alumno {
  font-size: 1.8rem;
  text-decoration: underline;
}
.pieza, .compositor {
  font-size: 1.5rem;
}
.maestro {
  font-size: 1.5rem;
  margin-bottom: 3.5rem;
}

/* About us */

.about-us {
 display: flex;
 justify-content: space-between;
 grid-gap: 20px;
 gap: 20px;
 padding: 0 10px;
 margin-top: 20px;
 margin-bottom: 30px;
}
.about-us-text {
  display: flex;
  align-items: center;
  font-size: 2.7rem;
}
.about-us-text h3 {
  margin: 0;
}
.about-us-text p {
  margin-top: 5px;
  text-align: justify;
}
.about-us-img {
  flex-basis:220px;
  flex-shrink: 0;
}
@media (max-width: 620px) {
  .about-us {
    flex-direction: column;
  }
  .about-us-text{ 
    order: 1;
  }
  .about-us-img {
    display: flex;
    justify-content: center;
    order: 2;
  }
  .about-us-img img {
    width: 150px;
  }
}

.form {
  background-color: #f7c331;
  background-color: var(--amarillo);
  max-width: 300px;
  margin: 2rem auto;
  border-radius: 1rem;
  padding: 2rem;
  display: flex;
  flex-direction: column;
  grid-gap: 1rem;
  gap: 1rem;
}
.form label {
  font-size: 2rem;
  font-family: 'Montserrat', sans-serif;
  font-family: var(--fuenteOrquestarte);
  color: #6b7a8f;
  color: var(--gris);
}
.form input {
  border: none;
  height: 4rem;
}
.form textarea {
  border: none;
  height: 7rem;
  resize: none;
  font-family: 'Montserrat', sans-serif;
  font-family: var(--fuenteOrquestarte);
}

.form-container {
  margin-top: 60px;
}

.form-container h2 {
  text-align: center;
  color: #6b7a8f;
  color: var(--gris);
}
.form button {
  background-color: #F7882F;
  background-color: var(--naranja);
  color: #ffffff;
  color: var(--blanco);
  font-family: 'Montserrat', sans-serif;
  font-family: var(--fuenteOrquestarte);
  font-size: 2rem;
  border: 0;
  border-radius: .5rem;
  height: 4rem;
}

.form span {
  color: #6b7a8f;
  color: var(--gris);
  font-size: 16px;
}

.form button:hover {
  cursor: pointer;
}

.maestro {
  font-size: 16px;
  color: #F7882F;
  color: var(--naranja)
}

.main-content-container {
  min-height: calc(100vh - 210px);
}

.error-message {
  color: red;
  font-size: 16px;
}

.form-link {
  color: #6b7a8f;
  color: var(--gris);
  font-size: 20px;
}

.form-link:hover {
  color: #F7882F;
}

.stretch-container {
  width: 650px;
  max-width: 80%;
  margin: 0 auto;
}
.months-table {
  max-width: 700px;
  width: 90%;
  font-size: 16px;
  text-align: center;
  color: var(--gris);
  margin: 0 auto;
}

.months-table thead {
  color: white;
  background-color: var(--gris);
}

.months-table td {
  padding: 20px;
}

.months-table tbody tr:nth-child(even){
  background-color: var(--amarillo);
}

.paid-month {
  background-color: rgb(62, 167, 62);
  color: white;
  border-radius: 10px;
}

.unpaid-month {
  background-color: var(--gris);
  color: white;
  border-radius: 10px;
}


.add-to-cart-button {
  background-color: var(--naranja);
  color: var(--blanco);
  font-family: var(--fuenteOrquestarte);
  font-size: 13px;
  border: 0;
  border-radius: .5rem;
  height: 4rem;
}

.add-to-cart-button:hover {
  background-color: var(--gris)
}

.remove-from-cart-button {
  background-color: var(--gris);
  cursor: pointer;
  color: var(--blanco);
  font-family: var(--fuenteOrquestarte);
  font-size: 13px;
  border: 0;
  border-radius: .5rem;
  height: 4rem;
}
